import React, { Component } from 'react';
import { Col } from 'react-bootstrap';

export default class AboutCard extends Component {
    render() {
        return (
            <Col lg={4}>
                <div href={this.props.link} className="about-card">
                    <img src={this.props.img_src} alt=""/>
                    <div className="about-card-text">
                        <div className="about-card-titles">
                            <h2>
                                {this.props.card_title_1}
                            </h2>
                            <h2>
                                {this.props.card_title_2}
                            </h2>
                        </div>
                        <p className="about-card-subtitle">
                            {this.props.card_subtitle}
                        </p>
                        <a href={this.props.link} className="about-card-link">
                            {this.props.link_text}
                        </a>
                    </div>
                </div>
            </Col>
        )
    }
}
