import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
 
export default class AboutRecomendations extends Component {
    render() {
        return (
            <section className="panangin-recomendations">
                <Container>
                    <Row className="justify-content-center">
                        <Col lg={5}>
                            <img src={this.props.img} alt=""/>
                        </Col>

                        <Col lg={7}>
                            <div className="recomendations-text">
                                <h3 className="panangin-title">{this.props.title}</h3>

                                <ul className="recomendations-text-list">
                                    <li className="recomendations-text-list-item">{this.props.list_item_1}</li>
                                    <li className="recomendations-text-list-item">{this.props.list_item_2}</li>
                                    <li className="recomendations-text-list-item">{this.props.list_item_3}</li>
                                    <li className="recomendations-text-list-item">{this.props.list_item_4}</li>
                                    <li className="recomendations-text-list-item">{this.props.list_item_5}</li>
                                </ul>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        )
    }
}
