import React, { Component } from 'react';

import PastHeader from '../../../Components/PagesComponents/PastHeader/PastHeaderSection.js';

import AboutPanangin from './AboutPanangin.js';
import PananginHeartSection from '../PananginHeartSection.js';
import AdvantagesCards from '../AdvantagesCards.js';
import AboutRecomendations from '../AboutRecomendations.js';
import AboutTimeTable from '../AboutTimeTable.js';

import PananginCard_1 from '../../../media/panangin-card-1.svg';
import PananginCard_2 from '../../../media/panangin-card-2.svg';
import PananginCard_3 from '../../../media/panangin-card-3.svg';
import RecomendationsImage from '../../../media/recomendations-image.jpg';
import TimeTableImage from '../../../media/time-table.png';

export default class Panangin extends Component {
    render() {
        return (
            <div>
                <PastHeader
                header_title={'О препаратах'}
                header_subtitle={'Узнайте больше о формах препарата Панангин'}
                />
                <AboutPanangin/>
                <PananginHeartSection
                    title={'Как действует Панангин?'}
                    list_item_1={'Панангин с калием и магнием – классический препарат для укрепления и питания миокарда, поддержания стабильной работы сердца и нормальной эластичности сосудов.'}
                    list_item_2={'Калий укрепляет миокард и обеспечивает стабильный сердечный ритм. Магний предотвращает спазм сосудов и улучшает их эластичность.'}
                    list_item_3={'Калий и магний в составе Панангина способствуют улучшению обмена веществ в миокарде и препятствует развитию атеросклероза.'}
                    list_item_4={'Доказано, что регулярный прием калия и магния помогает снизить риск возникновения инфаркта в 2 раза и риск развития инсульта на 40%.'}
                    list_item_5={'Кроме этого, активные вещества препарата способствуют мягкому снижению артериального давления на 6/3 мм рт. ст. *'}
                />

                <AdvantagesCards
                title={'Преимущества препарата Панангин'}
                card_title_1={'Легко усваивается'}
                front_side_1={PananginCard_1}
                back_side_1={'Калий и магний в виде органических солей – аспарагинатов с высокой биодоступностью.'}
                card_title_2={'Высокий профиль безопасности'}
                front_side_2={PananginCard_2}
                back_side_2={'Сбалансированные дозировки калия (158 мг) и магния (140 мг). Можно принимать длительно.'}
                card_title_3={'Сердечная формула'}
                front_side_3={PananginCard_3}
                back_side_3={'Содержит только полезные для сердца микроэлементы – калий и магний'}
                />

                <AboutRecomendations
                img={RecomendationsImage}
                title={'Кому рекомендован Панангин?'}
                list_item_1={'здоровым людям, рацион которых беден микроэлементами'}
                list_item_2={'при сердечной недостаточности и другими заболеваниями сердца'}
                list_item_3={'после перенесенного инфаркта миокарда'}
                list_item_4={'при нарушениях сердечного ритма'}
                list_item_5={'пациентам, постоянно принимающим диуретики, например, при артериальной гипертонии*'}
                />

                <AboutTimeTable
                title_1={'Схема приема препарата Панангин'}
                subtitle_1={'По 2 таблетки 3 раза в день после еды.'}
                subtitle_2={'Вопрос длительности курса следует обсудить с врачом.'}
                title_2={'Форма выпуска'}
                subtitle_3={'Таблетки, покрытые пленочной оболочкой, №50.'}
                subtitle_4={'Таблетки, покрытые пленочной оболочкой, №100.'}
                img={TimeTableImage}
                />
            </div>
        )
    }
}
