import React, { Component } from 'react';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import HeaderCarouselItemData from '../../../Data/HeaderCarouselItemData.js';
import CarouselItem from './CarouselItem.js';

export default class HeaderCarousel extends Component {
    render() {
        const CarouselItems = HeaderCarouselItemData.map(item => {
            return (
                <CarouselItem
                key={item.src}
                src={item.src}
                icon={item.icon}
                title={item.title}
                list_item_1={item.list_item_1}
                list_item_2={item.list_item_2}
                gif={item.gif}
                />
            )

        });

        return (

            <OwlCarousel items={1} loop nav autoplay autoplayTimeout="10000">
                {CarouselItems}
            </OwlCarousel>

        )
    }
}
