import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';

import HomeHeartDidKnow from './HomeHeartDidKnow.js';
import HomeHeartCard from './HomeHeartCard.js';
import BigHeart from '../../../../media/big-heart3.png';
import KIcon from '../../../../media/k-icon.png';
import MgIcon from '../../../../media/mg-icon.png';

export default class HomeHeartSection extends Component {
    render() {
        return (
            <section className="home-heart">
                <Container>
                    <h2 className="home-heart-title">Элементы здорового сердца</h2>

                    <Row className="justify-content-center">
                        <Col lg={6}>
                            <HomeHeartDidKnow/>
                        </Col>

                        <Col lg={6}>
                            <HomeHeartCard
                            title={'магний'}
                            li_1={'Улучшает тонус сердечной мышцы'}
                            li_2={'Поддерживает эластичность стенок сосудов'}
                            li_3={'Способствует снижению уровня холестерина'}
                            li_4={'Повышает устойчивость организма к стрессу'}
                            li_5={'Снижает уровень беспокойства и&nbsp;раздражительности'}
                            />
                        </Col>
                        <Col lg={6}>
                            <div className="big-heart">
                                <div className="k-icon">
                                    <img src={KIcon} alt=""/>
                                </div>
                                    <img className="heart-icon" src={BigHeart} alt=""/>
                                <div className="mg-icon">
                                    <img src={MgIcon} alt=""/>
                                </div>
                            </div>
                        </Col>

                        <Col lg={6}>
                            <HomeHeartCard
                            title={'калий'}
                            li_1={'Нормализует сердечные сокращения и сердечный ритм'}
                            li_2={'Оказывает кардиопротективное действие'}
                            li_3={'Способствует снижению давления на 3 – 6 мм рт.ст.'}
                            li_4={'Регулирует водно-солевой обмен'}
                            li_5={'Участвует в процессах образования энергии'}
                            />
                        </Col>

                    </Row>
                </Container>
            </section>
        )
    }
}
