import React, { Component } from 'react';
import { Container } from 'react-bootstrap';
import PananginAboutCard from '../PananginAboutCard.js';

import PananginForte from '../../../media/panangin-forte.png';
import RythmGif from '../../../media/l-rhythm.gif';
import Choose_1 from '../../../media/about-choose-1.png';
import Choose_2 from '../../../media/about-choose-2.png';

export default class AboutPananginForte extends Component {
    render() {
        return (
            <section className="panangin-about">
                <Container>
                    <PananginAboutCard
                    src={PananginForte}
                    gif={RythmGif}
                    title={'Ответственный за ритм'}
                    choose_1={Choose_1}
                    choose_2={Choose_2}
                    choose_text={'Панангин® - препарат выбора врачей и фармацевтов для поддержания стабильной работы сердца'} 
                    />
                </Container>
            </section>
        )
    }
}
