import React, { Component } from 'react';

export default class PastHeaderSection extends Component {
    render() {
        return (
            <section className="past-header">
                <h2 className="past-header-title">{this.props.header_title}</h2>
                <h3 className="past-header-subtitle">{this.props.header_subtitle}</h3>
            </section>
        )
    }
}
