import React, { Component } from 'react';

import NavbarWrapper from './NavbarWrapper.js';
import NavbarLogo from './NavbarLogo.js';

import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';

import Home from '../../Pages/_Home/_Home.js';
import About from '../../Pages/About/About.js';
import Instructions from '../../Pages/Instructions/Instructions.js';
import Panangin from '../../Pages/About/Panangin/Panangin.js';
import PananginForte from '../../Pages/About/PananginForte/PananginForte.js';

export default class Header extends Component {
    render() {
        return (
            <>
            <header className="header">
            
                
                <NavbarWrapper/>
                <NavbarLogo />
            </header>

            <Router>
                    <Switch>
                        <Route exact path="/" component={Home}/>
                        <Route exact path="/about" component={About}/>
                        <Route exact path="/instructions" component={Instructions}/>
                        <Route exact path="/about/about-panangin" component={Panangin}/>
                        <Route exact path="/about/about-panangin-forte" component={PananginForte}/>
                    </Switch>
            </Router>
            </>
        )
    }
}
