import React, { Component } from 'react';
import ToggleButton from './toggleButton.js';
import NavbarList from './NavbarList.js';

export default class NavbarWrapper extends Component {
    render() {
        return (
            <div 
            className="navbar-wrapper"
            id="navbar-wrapper"
            >
                <ToggleButton/>
                <NavbarList/>
            </div>
        )
    }
}