import React, { Component } from 'react';
import PastHeaderSection from '../../Components/PagesComponents/PastHeader/PastHeaderSection.js';
import AboutMainSection from '../../Components/PagesComponents/About/AboutMain/AboutMainSection.js';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';

import Panangin from './Panangin/Panangin.js';
import PananginForte from './PananginForte/PananginForte.js';

export default class About extends Component {
    render() {
        return (
            <>
            <div>
                <PastHeaderSection
                header_title={'О препаратах'}
                header_subtitle={'Узнайте больше о формах препарата Панангин'}
                />
                <AboutMainSection/>
            </div>

            <Router>
                <Switch>
                    <Route exact path="/about/about-panangin" component={Panangin}/>
                    <Route exact path="/about/about-panangin-forte" component={PananginForte}/>
                </Switch>
            </Router>
            </>
        )
    }
}
