import React, { Component } from 'react';

import { Container, Row, Col } from 'react-bootstrap';
import GedeonRichterLogo from '../../media/gedeon-richter-logo.svg';

export default class Footer extends Component {
    render() {
        return (
            <section className="footer">
                <Container>
                    <Row className="justify-content-center">
                        <Col md={6}>
                            <img className="gedeon-footer-logo" src={GedeonRichterLogo} alt=""/>
                        </Col>

                        <Col md={6}>
                            <div className="footer-content">
                                <span className="footer-content-title">
                                    Основано в 1901 году
                                </span>

                                <p className="footer-content-text">
                                    <span>ПРЕДСТАВИТЕЛЬСТВО ОАО &#171;ГЕДЕОН РИХТЕР&#187;В РЕСПУБЛИКЕ УЗБЕКИСТАН</span> <br/>
                                    100015,г.Ташкент, улица Нукус, 71, Республика Узбекистан <br/>
                                    Телефон: (99878)147-90-42 Факс:(99878)147-90-46 <br/>
                                    E-mail:gedeon@gr.uz
                                </p>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        )
    }
}
