import React, { Component } from 'react';
import { Container } from 'react-bootstrap';


export default class NavbarLogo extends Component {
    render() {
        return (
            <nav className="navbar">
                <Container>
                    <div className="logo-text">
                        <a href="/" className="logo">
                            Панангин
                        </a>
                    </div>
                </Container>
            </nav>
        )

    }
} 
