import React, { Component } from 'react';

import { Row } from 'react-bootstrap';

import AboutMainCardsData from '../../../Data/AboutMainCardsData.js';
import AboutCard from './AboutCard.js';

export default class AboutCards extends Component {
    render() {
        const AboutCards = AboutMainCardsData.map(item => {
            return (
                <AboutCard
                key={item.card_title_2}
                img_src={item.img_src}
                card_title_1={item.card_title_1}
                card_title_2={item.card_title_2}
                card_subtitle={item.card_subtitle}
                link_text={item.link_text}
                link={item.link}
                />
            )
        });

        return (
            <Row className="justify-content-center">
                {AboutCards}
            </Row>
        )
    }
}
