import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';


export default class CarouselItem extends Component {
    render() {
        return (
            <Container>
                <Row className="justify-content-center">
                    <Col lg={7}>
                        <div className="header-carousel-img">
                            <img src={this.props.src} alt=""/>
                            <a className="header-link" href="/instructions">Инструкция</a>
                        </div>
                    </Col>

                    <Col lg={5}>
                        <div className="header-carousel-content">
                            <div className="round-logo">
                                <img src={this.props.gif} alt=""/>
                            </div>
                            <div className="header-carousel-content-text">
                                <h2 className="header-carousel-content-title">{this.props.title}</h2>

                                <ul className="header-carousel-content-list">
                                    <li className="header-carousel-content-list-item">
                                        {this.props.list_item_1}
                                    </li>
                                    <li className="header-carousel-content-list-item">
                                        {this.props.list_item_2}
                                    </li>
                                </ul>

                                <a href="/about" className="header-link">Узнать больше</a>
                            </div>
                        </div>
                    </Col>
                </Row>
            </Container>
        )
    }
}
