import React, { Component } from 'react';
import { Container } from 'react-bootstrap';
import PananginAboutCard from '../PananginAboutCard.js';


import Panangin_50 from '../../../media/panangin-50.png';
import Panangin_100 from '../../../media/panangin-100.png';
import HeartGif from '../../../media/l-heart.gif';
import StressGif from '../../../media/l-stress.gif';
import Choose_1 from '../../../media/about-choose-1.png';
import Choose_2 from '../../../media/about-choose-2.png';

export default class AboutPanangin extends Component {
    render() {
        return (
            <section className="panangin-about">
                <Container>
                        <PananginAboutCard
                            src={Panangin_50}
                            gif={HeartGif}
                            title={'Классика для сердца'}
                            choose_1={Choose_1}
                            choose_2={Choose_2}
                            choose_text={'Панангин® - препарат выбора врачей и фармацевтов для поддержания стабильной работы сердца'}
                        />
                        <PananginAboutCard
                            src={Panangin_100}
                            gif={StressGif}
                            title={'Курс на крепкое сердце'}
                        />


                    <div className="panangin-about-benefits">
                        <ul className="panangin-about-benefits-list">
                            <li className="panangin-about-benefits-list-item">Укрепление сердечной мышцы</li>
                            <li className="panangin-about-benefits-list-item">Поддержка эластичности сосудов</li>
                        </ul>
                    </div>
                </Container>
            </section>
        )
    }
}
