import React, { Component } from 'react';

import { Row, Col } from 'react-bootstrap';

export default class PananginAboutCard extends Component {
    render() {
        return (
            <Row className="justify-content-center">
                    <Col lg={6}>
                        <div className="panangin-about-img">
                            <img src={this.props.src} alt=""/>
                            <div className="panangin-about-choose">
                                <img src={this.props.choose_1} alt=""/>
                                <img src={this.props.choose_2} alt=""/>
                                <p className="panangin-about-choose-text">
                                    {this.props.choose_text}
                                </p>
                            </div>
                        </div>
                    </Col>

                    <Col lg={6}>
                        <div className="panangin-about-content">
                            <div className="panangin-about-content-round-logo">
                                <img src={this.props.gif} alt=""/>
                            </div>
                            <div className="panangin-about-content-text">
                                <h2 className="panangin-about-content-title">{this.props.title}</h2>
                            </div>
                        </div>
                    </Col>
            </Row>
            
        )
    }
}
