import React, { Component } from 'react';

export default class ToggleButton extends Component {
    render() {
        const ButtonOnClick = () => {
            const html = document.querySelector('html');
            const body = document.querySelector('body');
            const btnToggler = document.querySelector('.btn-toggler');
            const wrapperMenu = document.querySelector('.navbar-wrapper');
        
        
            btnToggler.classList.toggle('is-active');
            btnToggler.classList.toggle('active');
            wrapperMenu.classList.toggle('active');

            if (wrapperMenu.classList.contains('active')) {
                html.style.overflowY = 'hidden';
                body.style.overflowY = 'hidden';
            }

            else if (!wrapperMenu.classList.contains('active')) {
                html.style.overflowY = 'scroll';
            }

        
        }
        return (
            <button 
            className="btn btn-toggler hamburger hamburger--squeeze"
            type="button"
            data-navbar-toggler="#navbar-wrapper"
            onClick={ButtonOnClick}
            >
                <span className="hamburger-box">
                    <span className="hamburger-inner"></span>
                </span>
            </button>
        );
    }
}


