import React, { Component } from 'react';

export default class HomeHeartCard extends Component {
    render() {
        return (
            <div className="heart-card">
                <h3 className="heart-card-title">{this.props.title}</h3>
                <ul className="heart-card-list">
                    <li className="heart-card-list-item">{this.props.li_1}</li>
                    <li className="heart-card-list-item">{this.props.li_2}</li>
                    <li className="heart-card-list-item">{this.props.li_3}</li>
                    <li className="heart-card-list-item">{this.props.li_4}</li>
                    <li className="heart-card-list-item">{this.props.li_5}</li>
                </ul>
            </div>
        )
    }
}
