import React, { Component } from 'react';

import PastHeader from '../../../Components/PagesComponents/PastHeader/PastHeaderSection.js';

import AboutPananginForte from './AboutPananginForte.js';
import PananginHeartSection from '../PananginHeartSection.js';
import AdvantagesCards from '../AdvantagesCards.js';
import AboutRecomendations from '../AboutRecomendations.js';
import AboutTimeTable from '../AboutTimeTable.js';

import PananginForteCard_1 from '../../../media/panangin-forte-card-1.svg';
import PananginForteCard_2 from '../../../media/panangin-forte-card-2.svg';
import PananginForteCard_3 from '../../../media/panangin-forte-card-3.svg';
import RecomendationsImage from '../../../media/recomendations-image-forte.jpg';
import TimeTableForteImage from '../../../media/time-table-forte.png';

export default class PananginForte extends Component {
    render() {
        return (
            <div>
                <PastHeader
                    header_title={'О препаратах'}
                    header_subtitle={'Узнайте больше о формах препарата Панангин Форте'}
                />

                <AboutPananginForte />
                <PananginHeartSection
                    title={'Как действует Панангин Форте?'}
                    list_item_1={'Панангин Форте, как и классический Панангин, содержит полезные для сердца минералы калий и магний в легко усваиваемой органической форме.'}
                    list_item_2={'Калий укрепляет миокард и обеспечивает стабильный сердечный ритм. Магний предотвращает спазм сосудов и улучшает их эластичность.'}
                    list_item_3={'Также эти микроэлементы важны для нормального процесса сокращения и расслабления мышц, что позволяет говорить об их особой важности при занятиях спортом.'}
                    list_item_4={'Калий и магний в составе Панангина способствуют улучшению обмена веществ в миокарде и препятствует развитию атеросклероза.'}
                    list_item_5={'Доказано, что регулярный прием калия и магния помогает снизить риск возникновения инфаркта в 2 раза и риск развития инсульта на 40%.'}
                />

                <AdvantagesCards
                    title={'Преимущества препарата Панангин'}
                    card_title_1={'Усиленная формула'}
                    front_side_1={PananginForteCard_1}
                    back_side_1={'В два раза больше калия (316 мг) и магния (280 мг) в одной таблетке* – более выраженный эффект.'}
                    card_title_2={'Удобство приема'}
                    front_side_2={PananginForteCard_2}
                    back_side_2={'Одна таблетка вместо двух на один прием и больше таблеток в упаковке*.'}
                    card_title_3={'Снижает сердечные риски'}
                    front_side_3={PananginForteCard_3}
                    back_side_3={'Калий и магний способствуют снижению риска развития инфаркта и инсульта**'}
                />

                <AboutRecomendations
                    img={RecomendationsImage}
                    title={'Кому рекомендован Панангин Форте?'}
                    list_item_1={'людям, с нарушениями сердечного ритма (аритмиями)'}
                    list_item_2={'при ишемической болезни сердца'}
                    list_item_3={'после перенесенного инфаркта миокарда'}
                    list_item_4={'при сердечной недостаточности (приме сердечных гликозидов)'}
                    list_item_5={'при дефиците калия и магния в рационе'}
                />

                <AboutTimeTable
                    title_1={'Схема приема препарата Панангин Форте'}
                    subtitle_1={'По 1 таблетке 3 раза в день после еды.'}
                    subtitle_2={'Вопрос длительности курса следует обсудить с врачом.'}
                    title_2={'Форма выпуска'}
                    subtitle_3={'Таблетки, покрытые пленочной оболочкой, №60.'}
                    img={TimeTableForteImage}
                />
            </div>
        )
    }
}
