import React, { Component } from 'react';

import { Container, Row, Col } from 'react-bootstrap';

export default class AdvantagesCards extends Component {
    render() {
        return (
            <section className="panangin-advantages">
                <Container>
                    <h3 className="panangin-title">{this.props.title}</h3>
                    <Row>
                        <Col lg={4}>
                            <div className="panangin-advantages-card">
                                <div className="panangin-advantages-card-title">
                                    {this.props.card_title_1}
                                </div>
                                <div className="panangin-advantages-card-spin">
                                    <div className="front-side">
                                        <img src={this.props.front_side_1} alt=""/>
                                    </div>
                                    <div className="back-side">
                                        <p>
                                            {this.props.back_side_1}
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col lg={4}>
                            <div className="panangin-advantages-card">
                                <div className="panangin-advantages-card-title">
                                    {this.props.card_title_2}
                                </div>
                                <div className="panangin-advantages-card-spin">
                                    <div className="front-side">
                                        <img src={this.props.front_side_2} alt=""/>
                                    </div>
                                    <div className="back-side">
                                        <p>
                                            {this.props.back_side_2}
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col lg={4}>
                            <div className="panangin-advantages-card">
                                <div className="panangin-advantages-card-title">
                                    {this.props.card_title_3}
                                </div>
                                <div className="panangin-advantages-card-spin">
                                    <div className="front-side">
                                        <img src={this.props.front_side_3} alt=""/>
                                    </div>
                                    <div className="back-side">
                                        <p>
                                            {this.props.back_side_3}
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        )
    }
}
