import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';

export default class AboutTimeTable extends Component {
    render() {
        return (
            <section className="panangin-timetable">
                <Container>
                    <h3 className="panangin-title">{this.props.title_1}</h3>
                    <div className="subtitles">
                        <p className="panangin-subtitle">{this.props.subtitle_1}</p>
                        <p className="panangin-subtitle">{this.props.subtitle_2}</p>
                    </div>

                    <Row>
                        <Col lg={12}>
                            <img src={this.props.img} alt=""/>
                        </Col>
                    </Row>

                    <h3 className="panangin-title">{this.props.title_2}</h3>
                    <div className="subtitles">
                        <p className="panangin-subtitle">{this.props.subtitle_3}</p>
                        <p className="panangin-subtitle">{this.props.subtitle_4}</p>
                    </div>
                </Container>
            </section>
        )
    }
}
