import React, { Component } from 'react';
import {Container, Row, Col } from 'react-bootstrap';

import BigHeart from '../../media/big-heart3.png';
import KIcon from '../../media/k-icon.png';
import MgIcon from '../../media/mg-icon.png';

export default class PananginHeartSection extends Component {
    render() {
        return (
            <section className="panangin-heart">
                <Container>
                    <Row className="justify-content-center">
                        <Col lg={6}>
                            <div className="panangin-heart-how">
                                <h3 className="panangin-title">{this.props.title}</h3>
                                <ul className="panangin-heart-how-list">
                                    <li className="panangin-heart-how-list-item">{this.props.list_item_1}</li>
                                    <li className="panangin-heart-how-list-item">{this.props.list_item_2}</li>
                                    <li className="panangin-heart-how-list-item">{this.props.list_item_3}</li>
                                    <li className="panangin-heart-how-list-item">{this.props.list_item_4}</li>
                                    <li className="panangin-heart-how-list-item">{this.props.list_item_5}</li>
                                </ul>
                            </div>
                        </Col>
                        <Col lg={6}>
                            <div className="heart">
                            <div className="k-icon">
                                    <img src={KIcon} alt=""/>
                                </div>
                                <img src={BigHeart} alt=""/>
                                <div className="mg-icon">
                                    <img src={MgIcon} alt=""/>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        )
    }
}
